/* TEXTFORM */
.placeholder-dark::placeholder {
    color: white; /* Placeholder text color for dark mode */
  }
  
.placeholder-light::placeholder {
    color: black; /* Placeholder text color for light mode */
}



/* NAVBAR */
.greenish,
.navbar.greenish,
.navbar.greenish .nav-link,
.navbar.greenish .form-check-label {
  background-color: rgb(0, 212, 0);
  color: white;
}

.light {
  background-color: aqua;
}

.navbar.pinkish,
.navbar.pinkish .nav-link,
.navbar.pinkish .form-check-label {
  background-color: pink;
}

.darkish,
.navbar.darkish,
.navbar.darkish .nav-link,
.navbar.darkish .form-check-label {
  background-color: black;
  color: white;
}

#inlineRadio1{
  background-color: greenyellow;
  cursor: pointer;
}

#inlineRadio1:checked{
  background-color: greenyellow;
  box-shadow: 0 0 0 0.2rem rgb(209, 255, 140);
  border: green;
}

#inlineRadio2{
  background-color: pink;
  cursor: pointer;
}


#inlineRadio2:checked{
  background-color: pink;
  box-shadow: 0 0 0 0.2rem rgb(255, 207, 215);
  border: rgb(255, 176, 189);
}

#inlineRadio3{
  background-color: #343a40;
  cursor: pointer;
}

#inlineRadio3:checked{
  background-color: #343a40;
  box-shadow: 0 0 0 0.2rem #a2a2a2;
  border: black;
}



